@import "utilities/variables";

#sidebar {
    height: 100%;
    width: 500px;
    position: fixed;
    top: 0px;
    left: -500px;
    background-color: #f8f8f8;
    overflow-x: hidden; // Disable scroll
    transition: 0.3s ease-in-out;
    z-index: 1000;
  
    @media screen and (max-width:$max-mobile-width) {
      width:100vw;
      left: -100vw;
    }
  
    #dismiss-sidebar-icon-container {
      position: relative;
      float: right;
      display: none;
      width: 34px;
      height: 34px;
      margin-top: 43px;
      margin-right: 5%;
      cursor: pointer;
  
      @media screen and (max-width:$max-mobile-width) {
        display: block;
      }
  
      #dismiss-sidebar-icon {
        position: absolute;
        display: inherit;
        margin: auto;
        width: 25;
        height: 25;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
      }
    }
  
    .picture {
      max-width: 250px;
      max-height: 250px;
      width: 20vh;
      height: 20vh;
      background-image: url(/assets/images/august.png);
      background-size: cover;
      margin: 0 auto;
      margin-top: 50px; 
  
      @media screen and (max-width:$max-mobile-width) {
        margin-top: 100px;
      }
    }
    
    p {
      text-align: center;
      margin: 0 auto;
      margin-top: 25px;
      width: 90%;
      max-width: 400px;
    }
  
    .hi { font-size: 24px; font-weight: 400; }
    .highlight { font-weight: 600; }
  
    .social-media-container {
      width: 100%;
      text-align: center;
      position: absolute;
      bottom: 0;
  
      @media screen and (max-width:$max-mobile-width) {
        position: static;
        margin-top: 30px;
        bottom: auto;
      }
    }
  
    .social-media-icons {
      display: inline-block;
      margin-bottom: 25px;
  
      img { padding: 20px; }
    }
  }
  
  #content { transition: 0.3s ease-in-out; }
  .translate-right {
    transition: 0.3s ease-in-out;
    transform: translate(500px, 0);
    @media screen and (max-width:$max-mobile-width) {
        transform: translate(100vw,0);
    }
  }