#logo-container {
    text-align: center;
    margin-top: 13px;
}

#logo {
    font-family: "Open Sans", $default-font;
    color: $red-color;
    @include text-gradient(45deg);
    font-size:30px;
}

#logo-prime {
    font-size: 50px;
    color:rgb(35,35,35);
}

#logo-tagline {
    font-family: "Open Sans", $default-font;
    font-size: 18px;
    color: rgba(162, 166, 184, 0.7);
    display: block;
    -webkit-font-smoothing: antialiased;

    @media screen and (max-width: $max-mobile-width) {
        display: none;
    }
}