@import "utilities/variables";
@import "utilities/mixins";
@import "components/header";
@import "components/logo";
@import "components/sidebar";

* { margin: 0; padding: 0; }
a { text-decoration: none; font-family: $default-font; }
h1, h2, h3 { font-family: $default-font; color: black; }
h1 { margin-bottom: 10px; font-size: 39px; font-weight: 600; }
h2 { margin-top: 50px; margin-bottom: 50px; font-weight: 600; font-size: 28px; }

main {
  width:90%;
  max-width: 1120px;
  margin:0 auto;
  margin-top: 80px;

  @media screen and (max-width: $max-mobile-width) {
    width:90%; margin-top: 20px;
  }
}

.date-ct {
  width: inherit;
  height: auto;
  margin-top: 20px;
  margin-bottom: 50px;

  .date-span {
    float:left;
    @media screen and (max-width:768px) {
      float: none;
      display: block;
    }
  }

  .date-span p { line-height:1.25em; font-weight:700; font-size:1.25em; color:rgba(162, 166, 184, 0.6); -webkit-font-smoothing:antialiased; text-align:left; }
  
  .github-span {
    float:right;
    @media screen and (max-width:768px) {
      float: none;
      display: block;
    }
  }
  .github-span img { float:left; margin-right:10px; margin-top:-2px; width: inherit; height: inherit; }
  .github-span a { float:left; @include text-gradient(45deg); font-weight:500; font-family: "Helvetica Neue"; }
}

ul, ol, p {
  font-weight: normal;
  font-weight: 300;
  color: black;
  font-family: $default-font;
  font-size: 18px;
  line-height: 40px;

  code { font-size:16px; padding:5px; background-color:rgb(250,250,250); }
}

.article-container {
  width: 100%;
  max-width: 800px;
  height: auto;
  margin: 0 auto;

  a { color: $red-color; }
}

.front-page-article-container {
  @extend .article-container;
  max-width:1120px;

  article { width:inherit; }
  h1 { width:60%; font-size:28px; float:left; margin-bottom: 50px; }
  p { width:60%; float:left; }
  .thumbnail-container { float:right; border-radius:2px; width:336px; height:224px; margin-top:75px; }
  .thumbnail { width: 100%; height:100%; }
  .thumbnail:active { opacity: 0.9; }

  .read-more-link { color:$red-color; @include text-gradient(45deg); font-family: "Open Sans", $default-font; font-weight:600; text-transform:uppercase; font-size:14px; }

  /* This prevents the thumbnail from pushing down the text when the
  screen size gets sufficiently narrow. */
  @media screen and (max-width: 1000px) {
    .thumbnail-container { width:35%; height: auto; }
  }

  @media screen and (max-width: 768px) {
    h1 { font-size: 22px; margin-top: 20px; margin-bottom: 20px; }
    h1, p { width:100%; float:none; }
    img { width: 100%; float:none; box-shadow: none; }
    .thumbnail-container { float:none; width:100%; height: auto; }
    .thumbnail { box-shadow: none; }
  }
}

.separating-line {
  width:100%;
  max-width:1120px;
  margin:0 auto;
  height:1px;
  background-color:rgba(162, 166, 184, 0.2);
  margin-bottom:100px;
  margin-top:100px;

  @media screen and (max-width: 768px) { margin: 20px 0 50px 0; }
}

#pagination-container {
  display: flex;
  width: 100%;
  height: 50px;
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin-top: -30px;
  margin-bottom: 40px;
}

.pagination-element {
  display: flex;
  width: 40px;
  height: 35px;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  cursor: pointer;
}

.pagination-element:hover {
  box-sizing: border-box;
  border: 1px solid $red-color;
}

.current-page {
  background-color: $red-color;
  p { color: white; }
}

article {
  width: inherit;
  margin-bottom: 30px;

  h1 { width: 100%; font-size: 28px; }
  p, ul { font-size:19px; width:100%; margin-bottom:30px; }
  img { display:block; margin:0 auto; }
  iframe { margin-bottom: 30px; }

  .imgwcaption { height: auto; width:750px; margin-top: 60px; margin-bottom: 60px; display:flex; align-items:center; }
  .imgwcaption img { margin: 0; width:70%; }
  .imgwcaption p { margin-left: 70px; width: 20%; font-size: 15px; line-height: 1.5; }

  ol { margin-bottom: 30px; }
  li { margin-bottom:5px; margin-left: 50px; width:inherit; }
  .post-title { text-align:left; font-size:50px; }
  .subtext {
    margin:0 auto;
    text-align:center;
    margin-bottom:30px;
    font-size:16px;
    width:80%;
    color:rgba(0,0,0,1);
    font-weight: bold;

    @media screen and (max-width: 768px) {
      width: 100%;
    }
  }
  .highlight { width:100%; margin-bottom:25px; }
  .katex { font-size: 1em !important; }
  .katex-display { font-size:22px; }
  .plot { font-family:Helvetica,Arial; text-align:center; width:inherit; }
  .scatter-plot { margin:20px 0 20px -33px; width:inherit; height:300px; }
  code { width:auto; font-family:Menlo,Monaco; line-height:27px; font-size:14px; border:1px solid rgb(231,235,238); margin-bottom: 30px; }

  @media screen and (max-width: 768px) {
    margin: 20px 0 50px 0;
    p { width: 100%; }
    img { width: 100%; }
    .post-title { font-size:40px; }
    .katex-display { overflow:auto; }
    .plot { font-family:Helvetica; overflow:scroll; }
    .scatter-plot { margin: 20px 0 20px -20px; }
    ul { width: 90%; margin: 0 auto; }
  }
}

footer {
  width: 100%;
  max-width: 800px;
  margin: 0 auto;

  .inner-footer { margin:0 auto; border-top:1px solid rgb(231,235,238); padding:30px 0 30px 0; width:inherit; }
}

.archive-year { color:rgb(51,55,73); font-size:36px; font-weight:600; }

.archive-list {
  list-style: none;
  text-indent: 20px;

  li { width:100%; }
}

.archive-day { color:rgba(51,55,73,0.5); }
.archive-link { font-weight:600; }