@import "utilities/variables";

header {
    display: flex;
    width: 100%;
    height: 140px;
    flex-direction: row;
    justify-content: center;

    @media screen and (max-width: $max-mobile-width) {
      height:40px;
    }
}

#hamburger {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
  position: absolute;
  width: 42px;
  height: 34px;
  float: left;
  margin-top: 40px;
  margin-left: 40px;
  cursor: pointer;

  .hamburger-bar {
    width:34px;
    height:3px;
    background-color: black;
  }

  @media screen and (max-width: $max-mobile-width) {
      margin-left: 5%; // Matches indentation of page content
  }
}